        transition: all 0.3s ease; }
      .container .Resume-button .Resume-category:before {
        content: '<';
        left: 0px;
        -webkit-transform: translateX(1rem);
                transform: translateX(1rem); }
      .container .Resume-button .Resume-category:after {
        content: "/>";
        right: 0px;
        -webkit-transform: translateX(-1rem);
                transform: translateX(-1rem); }
      .container .Resume-button .Resume-category:hover:before {
        opacity: 1;
        -webkit-transform: translateX(-1.5rem);
                transform: translateX(-1.5rem); }
      .container .Resume-button .Resume-category:hover:after {
        opacity: 1;
